.homeContainer {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100vw;
  max-width: 100vw;
  height: fit-content;
  overflow-x: hidden;
  background-color: rgba(165, 42, 42, 0.89);
  font-family: Dancing Script, Verdana, Geneva, Tahoma, sans-serif;
}

#homeHeader {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  height: 70vh;
  width: 100vw;
  max-width: 100vw;
  overflow: hidden;
  overflow-wrap: break-word;
  font-size: 20px;
 
  background-image: url('../../assets/images/responsive.jfif');
  background-position-x: right;
  background-position-y: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #3a3535;
}

#homeHeader article {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  overflow-wrap: break-word;
  width: 100%;
  height: 100%;
  animation-name: move;
  animation-duration: 2s;
}
@keyframes move {
  from {
    opacity: 0;
    transform: translateX(35%);
  }to {
    opacity: 1;
    transform: translateX(0%);
  }
}

.rellax {
  display: flex;
  position: relative;
}

.rellax p {
  position: relative;
  white-space: nowrap;
  text-shadow:-.3px 0 black, 0 .3px black, .3px 0 black, 0 -.3px black;
}

#front-end {
  color: rgb(23, 214, 228);
  top: 50px;
  left: 10%;
}
#back-end {
  color: rgb(230, 71, 230);
  top: 80px;
  left: 25%;
}
#responsive {
  align-self: center;
  top: 50%;
  color: rgb(20, 214, 20);
}
#web {
  color: yellow;
  top: -70px;
  left: 47%;
}
#mobile {
  color: rgb(111, 52, 126);
  top: -30px;
  left: 65%;
}
#rest {
  position: relative;
  right: -89vw;
  top: 40%;
  margin-right: 10px;
  color: rgb(170, 8, 8);
}

#techs {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
  width: 100vw;
  height: 100%;
  min-height: fit-content;
  padding-bottom: 25px;
  
  /* background-color: #3a3535; */
  color: #fff;
}
#techs p{
  font-size: 2.5em;
  text-shadow:-.3px 0 black, 0 .3px black, .3px 0 black, 0 -.3px black;
  color: #21b64e; 
  margin-top: 25px;
}

#techs ul {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex-wrap: wrap;
  margin: 5px;
  margin-top: 25px;
  padding-right: 50px;
}
#techs ul li {
  display: flex;
  list-style: none;
  font-family: Dancing Script, Verdana, Geneva, Tahoma, sans-serif;
  font-size: 1.5em;
  margin: 5px;
}

#techs img {
  display: flex;
  height: 145px;
  width: 145px;
  background-color: #fff;
}

.works {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
  height: 75vh;
  width: 100vw;
  margin: 0;
  padding: 15px;
  background-color: #232020;
  font-size: 1em;
  color: #fff;
  text-shadow:-.3px 0 black, 0 .3px black, .3px 0 black, 0 -.3px black;
  font-family: OpenSans-Light, 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.works p:nth-of-type(1) {
  color: azure;
  font-family: Dancing Script, Verdana, Geneva, Tahoma, sans-serif;
  font-size: 2.5em;
}
.works p:nth-of-type(2) {
  align-self: flex-start;
  color: #fff;
  font-size: 1em;
}

.works-container {
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.work-div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 25vw;
  height: 100%;
  transform: skew(-8deg, 0deg);
  margin-right: 2px;
  transition: .8s all ease;
}
.work-div:hover {
  z-index: 1;
  justify-self: center;
  align-items: center;
  height: 100%;
  cursor: pointer;
  width: 85vw;
  background-size: cover;
  background-repeat: no-repeat;
  transition: 1s all ease;
  
}

.mobile:hover {
  transform: skew(0deg, 0deg);
  transition: 1s all ease;


}


.savoureux {
  background-image: url('./images/restaurant-savoureux.png');
  background-position-x: center;
  background-size: cover;
}

.topmovies {
  background-image: url('./images/topmovies.jpg');
  background-position-x: center;
  background-size: cover;
}
.topmovies:hover {
  width: 240px;
  margin-right: 40px;
}

.instagrand {
  background-image: url('./images/topmovies.png');
  background-position-x: center;
  background-size: cover;
}
.instagrand:hover {
  width: 210px;
  margin-left: 40px;
}

 .works button {
   align-self: flex-end;
   outline: none;
   margin-right: 45px;
   color: #fff;
   background-color: #21b64e;
   border-style: none;
   height: 45px;
 }




